<template>
  <div class="container">
    <div class="user-info">
      <div>用户信息</div>
      <div>购买金额:{{showPay}}</div>
      <div>送货地址</div>
      <div>支付</div>
    </div>
    <div class="sp-car">
      <h3>购物车</h3>
      <div class="msg" :class="{active : !goodsData.length}">购物车为空！</div>
      <div class="item" v-for="item in goodsData" :key="item.key">
        商品名：{{item.name}}<br />
        商品单价：{{item.price}}<br />
        商品数量：{{item.number}}
      </div>
    </div>
  </div>
</template>
<script>
import store from '../counter/store.js'
  export default {
    created(){console.log('created')
      wx.setStorage({
        key:"key",
        data:"value",
        success: () => {
          console.log("缓存成功！")
        }
    })
      wx.login({
        success: function(res) {
          if (res.code) {
            //发起网络请求
            wx.request({
              url: 'https://test.com/onLogin',
              data: {
                code: res.code
              }
            })
          } else {
            console.log('登录失败！' + res.errMsg)
          }
        }
    });
    },
    mounted(){console.log(store.getters.allPay)},
    data(){
      return {
        name : 'user',
      }
    },
    computed: {
        goodsData: () => {
            return store.state.shopping
        },
        showPay: () => {
          return store.getters.allPay
        }
    }
  }
</script>
<style lang="less" scoped>
@maxW: 750rpx;
  .user-info{
    display: flex;
    flex-flow: row nowrap;
    width: @maxW;
    height: 200rpx;
    background: #ccc;
    div{
      width:25%;
      height: 100%;
      background: black;
      border: 4rpx solid #fff;
      color: #fff
    }
  }
  .sp-car{
    display: flex;
    flex-flow: row wrap;
    width: @maxW;
    background: #eee;
    padding-bottom: 14rpx;
    h3{
        height: 120rpx;
        background: #fff;
        width: @maxW;
        line-height: 120rpx;
        text-align: center;
        margin-bottom: 14rpx;
    }
  }
  .item{
      width: @maxW - 28rpx;
      height: 200rpx;
      background: #fff;
      margin: 14rpx;
      border-radius: 6rpx
  }
  .msg{
    display: none
  }
  .active{
    display: block;
    text-align: center
  }
</style>



